@import url("https://fonts.googleapis.com/css?family=Red+Hat+Display&display=swap");

* {
    box-sizing: border-box;
    font-family: "Red Hat Display";
    margin: 0;
    color: white;
    text-shadow: 0.07em 0.07em 0 black;
}

body {
    height: 100%;
    background-image: url("https://images.pexels.com/photos/1227648/pexels-photo-1227648.jpeg?auto=compress&cs=tinysrgb&dpr=2&h=750&w=1260");
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    background-attachment: fixed;
}

.loading {
    text-align: center;
    font-size: 4em;
    font-weight: bold;
    color: #fff;
}

.app {
    text-align: center;
}

.home-page-header {
    font-size: 5em;
    display: flex;
    align-items: center;
    justify-content: center;
}

.artist-counter {
    color: #fff;
    font-size: 2em;
    margin: 2px;
    display: inline-block;
    text-align: center;
}

.filter {
    color: #fff;
    font-size: 1.2em;
    margin: 10px;
    padding-left: 1%;
    padding-right: 1%;
    display: flex;
    justify-content: center;
}

.filter span {
    padding-right: 10px;
}

.filter-input {
    background-color: #fff;
    color: black;
    text-shadow: none;
}

ul {
    padding: 0;
}

li {
    display: inline-block;
}

.album-content {
    background-color: rgba(255, 255, 255, 0.3);
    margin: 5px;
    display: inline-flex;
}

.description {
    margin: 10px;
    text-align: left;
    width: 250px;
}

.artist-name {
    margin-top: 5%;
    font-size: 1.6em;
    font-weight: bold;
}

.album-title {
    margin-top: 5%;
    font-size: 1.25em;
    font-weight: normal;
}

.release-date {
    margin-top: 5%;
    font-size: 1em;
    font-weight: normal;
}

.album-cover {
    width: 315px;
    height: 315px;
}

.open-in-spotify-btn {
    font-size: 1.2em;
    color: black;
    text-shadow: none;
    background-color: #1ed761;
    margin-top: 10px;
}

.open-in-spotify-btn:hover {
    opacity: 0.8;
    cursor: pointer;
}

.sign-in-button {
    display: inline-block;
    font-size: 2em;
    color: black;
    text-shadow: none;
    background-color: #1ed761;
    border-color: black;
    margin: 10px;
}
